#wrapper00 {
  background-image: url(../assets/home/background-home-mobile.webp);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 90px 4.5fr 4fr;
  grid-template-areas: "navbar00" "detailsContainer00" "whiteBallContainer00";
}
#wrapper00 div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  text-align: center;
}

#detailsContainer00 {
  grid-area: detailsContainer00;
}

#heading00 {
  color: #fff;
  text-transform: uppercase;
  width: 100%;
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 5rem;
  line-height: 125%;
  margin: 1rem 0 1.5rem 0;
}

#headSpan00 {
  color: #d0d6f9;
  width: 100%;
  text-transform: uppercase;
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.1875rem;
  text-align: center;
  letter-spacing: 0.16875rem;
}

#bodyText00 {
  color: #d0d6f9;
  width: 100%;
  padding: 0 1.5rem;
  font-weight: 400;
  font-family: "Barlow";
  font-style: normal;
  font-size: 0.9375rem;
  line-height: 167%;
}

#whiteBallContainer00 {
  grid-area: whiteBallContainer00;
}

#whiteBall00 {
  background-color: #fff;
  height: 150px;
  width: 150px;
  border-radius: 50%;
  transition: box-shadow 0.5s;
}
#whiteBall00:hover {
  box-shadow: 0 0 0 50px rgba(255, 255, 255, 0.25);
}

#whiteBallText00 {
  color: #0b0d17;
  text-transform: uppercase;
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 1.25rem;
  text-align: center;
  letter-spacing: 0.078125rem;
}

#navbar00 {
  grid-area: navbar00;
}

@media screen and (min-width: 767px) and (max-width: 1023.5px) {
  #wrapper00 {
    background-image: url(../assets/home/background-home-tablet.webp);
  }
  #headSpan00 {
    font-size: 1.25rem;
    line-height: 1.5rem;
    letter-spacing: 0.2109375rem;
  }
  #heading00 {
    font-size: 9.375rem;
    line-height: 100%;
    margin: 2rem 0 2rem 0;
  }
  #bodyText00 {
    padding: 0 10rem;
    font-size: 1rem;
    line-height: 175%;
  }
  #whiteBall00 {
    height: 242px;
    width: 242px;
  }
  #whiteBall00:hover {
    box-shadow: 0 0 0 80px rgba(255, 255, 255, 0.25);
  }
  #whiteBallText00 {
    letter-spacing: 0.125rem;
    font-size: 2rem;
  }
  #detailsContainer00 {
    position: relative;
  }
  #detailsContainerChild00 {
    position: relative;
    top: 25%;
    transform: translateY(-25%);
  }
}
@media screen and (min-width: 1024px) {
  #wrapper00 {
    background-image: url(../assets/home/background-home-desktop.webp);
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 4.5fr 4fr;
    grid-template-areas: "navbar00 navbar00" "detailsContainer00 whiteBallContainer00" "detailsContainer00 whiteBallContainer00";
    height: 100%;
  }
  #headSpan00 {
    font-size: 1.75rem;
    width: 425px;
    text-align: left;
  }
  #heading00 {
    font-size: 9.375rem;
  }
  #bodyText00 {
    font-size: 1.125rem;
    line-height: 2rem;
    padding: 0;
    text-align: left;
    width: 425px;
  }
  #whiteBall00 {
    height: 274px;
    width: 274px;
    transform: translate(20%, 40%);
  }
  #whiteBall00:hover {
    box-shadow: 0 0 0 100px rgba(255, 255, 255, 0.25);
  }
  #whiteBallText00 {
    font-size: 2rem;
  }
  #detailsContainerChild00 {
    transform: translateY(15%);
  }
