#wrapper01 {
  background: #0b0d17 url(../assets/destination/background-destination-mobile.webp) no-repeat center center;
  background-size: cover;
}

#dvh100Container {
  height: 100svh;
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 90px 1fr 1fr;
  grid-template-areas: "navbar01" "imgDetails01" "textDetails01";
}

.inlineBlock {
  display: none;
}

.detailsContainer01 {
  text-align: center;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
  color: #d0d6f9;
}
.detailsContainer01 span, .detailsContainer01 ul, .detailsContainer01 h2, .detailsContainer01 p, .detailsContainer01 div {
  width: 100%;
}

#spanHeading01 {
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.1875rem;
  letter-spacing: 0.16875rem;
  text-transform: uppercase;
  color: #fff;
}

#spanHeadingNumber01 {
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.1875rem;
  letter-spacing: 0.16875rem;
  color: #fff;
  mix-blend-mode: normal;
  opacity: 0.25;
  position: relative;
  left: -1rem;
}

#planetImg {
  height: 170px;
  width: 170px;
  margin-top: 2rem;
}

.planetNameLinks {
  display: inline;
  text-transform: uppercase;
  margin: 0 0.75rem;
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.0625rem;
  letter-spacing: 0.14765625rem;
  color: #d0d6f9;
  text-decoration: 2.5px underline transparent;
  text-underline-offset: 10px;
  transition: text-decoration-color 0.5s;
  cursor: pointer;
}

#heading01 {
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 3.5rem;
  line-height: 4rem;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  margin: 2rem 0 1.25rem 0;
}

#bodyText01 {
  font-family: "Barlow";
  font-style: normal;
  font-weight: 400;
  font-size: 0.9375rem;
  line-height: 167%;
  text-align: center;
  color: #d0d6f9;
  padding: 0 1.5rem;
}

.activePlanetNameLink {
  color: #fff !important;
  text-decoration-color: #fff;
}

#hr01 {
  position: absolute;
  bottom: 0;
  width: 85%;
  left: 50%;
  transform: translateX(-50%);
  border-color: #383B4B;
}

.travelHeadings01 {
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 0.875rem;
  text-align: center;
  letter-spacing: 0.14765625rem;
  text-transform: uppercase;
  color: #d0d6f9;
}

.travelDetails01 {
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 1.75rem;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  margin-top: 1rem;
}

.marginTop01 {
  margin-top: 2rem;
}

.margin01 {
  margin: 2rem 0;
}

#imgDetails01 {
  grid-area: imgDetails01;
}

#textDetails01 {
  grid-area: textDetails01;
}

#navbar01 {
  grid-area: navbar01;
}

@media screen and (min-width: 767px) and (max-width: 1023.5px) {
  #wrapper01 {
    height: 100%;
    background-image: url(../assets/destination/background-destination-tablet.webp);
  }
  #spanHeading01 {
    text-align: left;
    font-size: 1.25rem;
    margin: 3rem 0 2rem 4rem;
  }
  #spanHeadingNumber01 {
    font-size: 1.25rem;
  }
  #dvh100Container {
    height: 85%;
  }
  #flexcenter {
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
  #planetImg {
    height: 300px;
    width: 300px;
  }
  .planetNameLinks {
    font-size: 1rem;
  }
  #heading01 {
    font-size: 5rem;
    margin: 1rem 0 1.5rem 0;
  }
  #bodyText01 {
    font-size: 1rem;
    line-height: 1.75rem;
    padding: 0 6rem;
  }
  #textDetails01 {
    margin-top: 3rem;
  }
  #hr01 {
    width: 80%;
  }
  #dvh20Container {
    height: 15%;
  }
  #BottomContainer011 {
    display: inline-flex;
    width: 40%;
    height: 100%;
    margin-top: 0;
    align-items: center;
  }
  #BottomContainer012 {
    display: inline-flex;
    width: 40%;
    height: 100%;
    margin-top: 0;
    align-items: center;
  }
  .travelHeadings01 {
    position: relative;
    bottom: 1.5rem;
  }
  .travelDetails01 {
    position: relative;
    bottom: 0.5rem;
  }
}
@media screen and (min-width: 1024px) {
  #wrapper01 {
    overflow-x: hidden;
    background-image: url(../assets/destination/background-destination-desktop.webp);
  }
  #dvh100Container {
    height: 100svh;
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 136px 1fr 1fr;
    grid-template-areas: "navbar01 navbar01" "imgDetails01 textDetails01" "imgDetails01 textDetails01";
  }
  #textDetails01 {
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateX(12.5%);
    overflow: visible;
  }
  #spanHeading01, #spanHeadingNumber01 {
    font-size: 1.75rem;
    letter-spacing: 0.295rem;
    display: inline;
    margin: 4rem 0 0 0;
  }
  #planetImg {
    height: 445px;
    width: 445px;
    transform: translateX(20%);
  }
  #planetNameList {
    text-align: left;
    margin-top: 1rem;
  }
  #flexcenter {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
  .planetNameLinks {
    font-size: 1rem;
    text-align: left;
    margin: 0 1rem;
  }
  #heading01 {
    font-size: 6.25rem;
    margin: 5rem 0 4rem 1rem;
    text-align: left;
  }
  #bodyText01 {
    font-size: 1.125rem;
    line-height: 2rem;
    text-align: left;
    padding: 0;
    width: 450px;
    margin: 0 0 1rem 1rem;
  }
  #dvh20Container {
    display: none;
  }
  #dvh201Container {
    display: block;
    text-align: left;
    margin: 2rem 0 0 1rem;
  }
  .inlineBlock {
    display: inline-block;
    width: 200px !important;
    margin-right: 2rem;
  }
  #hr0101 {
    border: 1px solid rgba(255, 255, 255, 0.14);
    width: 450px;
    margin-bottom: 2rem;
  }
  #hr01 {
    display: none;
  }
  .travelHeadings01 {
    font-size: 0.875rem;
    display: block;
    text-align: left;
  }
  .travelDetails01 {
    font-size: 1.75rem;
  }
}