header {
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 90px;
  position: relative;
  z-index: 999;
}
header img {
  margin: 0 1.5rem;
}

#hr00 {
  display: none;
}

#homeIcon {
  height: 40px;
  width: 40px;
}

/* NAV */
#nav {
  color: #fff;
  position: fixed;
  height: 100vh;
  width: 254px;
  right: -254px;
  top: 0;
  -webkit-backdrop-filter: blur(35px);
          backdrop-filter: blur(35px);
  background-color: rgba(255, 255, 255, 0.04);
  transition: border-color 0.5s;
}

#crossToClose {
  float: right;
  margin: 2rem 1.5rem;
}

#navList {
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  line-height: 19px;
  line-height: 1.1875rem;
  letter-spacing: 0.16875rem;
  list-style-type: none;
  margin-top: 7.5rem;
}
#navList li {
  margin: 2.5rem;
  cursor: pointer;
}
#navList span {
  font-weight: 700;
  margin-right: 1rem;
}

@media screen and (min-width: 767px) and (max-width: 1023.5px) {
  header {
    height: 96px;
  }
  .isActiveLi {
    border-color: #fff !important;
  }
  #homeIcon {
    height: 48px;
    width: 48px;
    margin-left: 2rem;
  }
  #burgerToOpen {
    display: none;
  }
  #crossToClose {
    display: none;
  }
  #nav {
    position: relative;
    height: 100%;
    width: 60%;
    right: 0;
    -webkit-backdrop-filter: blur(35px);
            backdrop-filter: blur(35px);
  }
  #navList {
    margin-top: 0;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    height: 100%;
    padding: 0 2rem;
    font-size: 0.875rem;
  }
  #navList li {
    margin: 0 1rem;
    display: inline-block;
    height: 100%;
    display: flex;
    align-items: center;
    border-bottom: 3px solid transparent;
    transition: border-color 0.5s;
  }
  #navList li:hover {
    border-color: rgba(255, 255, 255, 0.4);
  }
  #navList span {
    display: none;
  }
}
